.player-bubble-hide
{
	display: none;
}

.player-bubble-hide:after
{
	display: none;
}


.player-bubble-show
{
	font-size: 1.2em;
	text-align: center;
	line-height: 3em;
	position: relative;
	width: 175px;
	height: 120px;
	padding: 0px;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	border-radius: 10px;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#fb83fa+15,e93cec+54 */
	background: #fb83fa; /* Old browsers */
	background: -moz-linear-gradient(top,  #fb83fa 15%, #e93cec 54%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top,  #fb83fa 15%,#e93cec 54%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom,  #fb83fa 15%,#e93cec 54%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fb83fa', endColorstr='#e93cec',GradientType=0 ); /* IE6-9 */
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	border-radius: 10px;
}

.player-bubble-show:after
{
	border-color: transparent #fb83fa;
	content: '';
	position: absolute;
	border-style: solid;
	border-width: 15px 0 15px 31px;
	display: block;
	width: 0;
	z-index: 1;
	right: -31px;
	top: 15px;
}


.cpu-bubble-hide
{
	display: none;
}

.cpu-bubble-hide:after
{
	display: none;
}

.cpu-bubble-show
{
	font-size: 1.3em;
	text-align: center;
	line-height: 2em;
	position: relative;
	width: 280px;
	height: 75px;
	padding: 0px;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#e5e696+18,d1d360+47 */
	background: #e5e696; /* Old browsers */
	background: -moz-linear-gradient(top,  #e5e696 18%, #d1d360 47%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top,  #e5e696 18%,#d1d360 47%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom,  #e5e696 18%,#d1d360 47%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e5e696', endColorstr='#d1d360',GradientType=0 ); /* IE6-9 */

	-webkit-border-radius: 9px;
	-moz-border-radius: 9px;
	border-radius: 9px;
}

.cpu-bubble-show:after
{
	content: '';
	position: absolute;
	border-style: solid;
	border-width: 13px 0 13px 30px;
	border-color: transparent #e5e696;
	display: block;
	width: 0;
	z-index: 1;
	right: -30px;
	top: 14px;
}

